.markdown {
  --post-builtin-themes: default, blue;
  --post-theme-default-color: #f7c600;
  --post-theme-blue-color: #0e5efe;
  --post-theme: default;
  --post-color-primary: #f7c600;
  --post-color-primary-bg: #fceaaf;
  --post-color-primary-text: #f6bc00;
  --post-color-text: #141414;
  --post-color-text-secondary: #8c8c8c;
  --post-color-on-primary: #fff;
  --post-color-on-primary-bg: #141414;
  --post-font-size-base: 16px;
  --post-font-size-h1: 2em;
  --post-font-size-h2: 1.625em;
  --post-font-size-h3: 1.375em;
  --post-font-size-h4: 1.125em;
  --post-font-size-h5: .875em;
  --post-font-size-h6: .75em;
  --post-font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  --post-font-family-code: monospace, Menlo, Monaco, Courier New;
  --post-p-font-size: 1.125em;
  --post-codeblock-bg: #434343;
  --post-codeblock-text-color: #fff;
  --post-codeblock-font-family: monospace, Menlo, Monaco, Courier New;
  --post-codeblock-font-size: 1em;
  --post-table-border-color: #d9d9d9;
  --post-table-bg: #fafafa;
  --post-blockquote-text-color: #8c8c8c;
  --post-blockquote-symbol-content: "“";
  --post-blockquote-symbol-color: rgba(140, 140, 140, .5);
  --post-blockquote-symbol-size: 60px;
  --post-blockquote-symbol-bg: transparent;
  --post-hr-height: 4px;
  --post-hr-bg: #f0f0f0;
  --post-list-bg-color: #fceaaf;
  --post-link-text-color: #f6bc00;
  --post-link-code-text-color: #f6bc00;
  --post-code-text-color: #141414;
  --post-code-bg-color: #fceaaf;
  --post-highlight-text-color: #fff;
  -webkit-text-size-adjust: 100%;
  font-family: var(--post-font-family);
  font-size: var(--post-font-size-base);
  color: var(--post-color-text);
}

.markdown.theme-blue {
  --post-theme: blue;
  --post-color-primary: #0e5efe;
  --post-color-primary-bg: #e2f3ff;
  --post-color-primary-text: #0e5efe;
  --post-color-text: #1f1f1f;
  --post-color-text-secondary: #8c8c8c;
  --post-color-on-primary: #fff;
  --post-color-on-primary-bg: #2b36df;
  --post-font-size-base: 14px;
  --post-font-size-h1: 2em;
  --post-font-size-h2: 1.625em;
  --post-font-size-h3: 1.375em;
  --post-font-size-h4: 1.125em;
  --post-font-size-h5: 1em;
  --post-font-size-h6: 1em;
  --post-font-family: -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  --post-font-family-code: Menlo, Monaco, Consolas, Ubuntu Mono;
  --post-codeblock-bg: #141414;
  --post-table-bg: rgba(226, 243, 255, .2);
  --post-blockquote-text-color: #595959;
  --post-blockquote-symbol-bg: #f0f0f0;
  --post-blockquote-symbol-content: "";
  --post-blockquote-symbol-size: 40px;
  --post-hr-height: 4px;
  --post-hr-bg: #f0f0f0;
  --post-p-font-size: 1.125em;
  --post-codeblock-text-color: #fff;
  --post-codeblock-font-family: Menlo, Monaco, Consolas, Ubuntu Mono;
  --post-codeblock-font-size: 1em;
  --post-table-border-color: #d9d9d9;
  --post-blockquote-symbol-color: rgba(140, 140, 140, .5);
  --post-list-bg-color: #e2f3ff;
  --post-link-text-color: #0e5efe;
  --post-link-code-text-color: #0e5efe;
  --post-code-text-color: #2b36df;
  --post-code-bg-color: #e2f3ff;
  --post-highlight-text-color: #fff;
}

.markdown blockquote {
  padding: 0 20px 0 var(--post-blockquote-symbol-size);
  color: var(--post-blockquote-text-color);
  margin: 2.25em 0;
  position: relative;
}

.markdown blockquote:before {
  content: var(--post-blockquote-symbol-content);
  min-width: 8px;
  text-align: center;
  color: var(--post-blockquote-symbol-color);
  background: var(--post-blockquote-symbol-bg);
  font-family: Helvetica Neue, Arial, Times New Roman, Impact;
  font-size: 5em;
  font-weight: bold;
  line-height: .6em;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.markdown blockquote > :last-child {
  margin-bottom: 0;
}

.markdown code {
  background: var(--post-code-bg-color);
  color: var(--post-code-text-color);
  border-radius: 3px;
  margin-left: 4px;
  margin-right: 4px;
  padding: 2px 5px;
  font-family: inherit;
}

.markdown pre {
  background: var(--post-codeblock-bg);
  border-radius: 6px;
  margin-top: 0;
  margin-bottom: 25px;
  padding: 16px 20px;
  overflow: auto;
}

.markdown pre > code {
  line-height: 1.5;
  font-size: var(--post-codeblock-font-size);
  color: var(--post-codeblock-text-color);
  font-family: var(--post-codeblock-font-family);
  background: none;
  margin: 0;
  padding: 0;
}

.markdown h1 {
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-size: 2em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown h1:first-child {
  margin-top: 0;
}

.markdown h2 {
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-size: 1.625em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown h2:first-child {
  margin-top: 0;
}

.markdown h3 {
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-size: 1.375em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown h3:first-child {
  margin-top: 0;
}

.markdown h4 {
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-size: 1.125em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown h4:first-child {
  margin-top: 0;
}

.markdown h5 {
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-size: 1em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown h5:first-child {
  margin-top: 0;
}

.markdown h6 {
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-size: 1em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown h6:first-child {
  margin-top: 0;
}

.markdown hr {
  height: var(--post-hr-height);
  background: var(--post-hr-bg);
  border: 0;
  margin: 1.6em 0;
}

.markdown img {
  max-width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
}

.markdown a {
  color: var(--post-link-text-color);
  text-decoration: none;
}

.markdown a code {
  color: var(--post-link-code-text-color);
}

.markdown a:active {
  opacity: .7;
}

.markdown a:hover {
  text-decoration: underline;
}

.markdown ol, .markdown ul {
  margin-top: 0;
  margin-bottom: 1.5em;
  padding-left: 1em;
}

.markdown ol ol, .markdown ol ul, .markdown ul ol, .markdown ul ul {
  margin-bottom: 0;
  padding-left: 1.75em;
}

.markdown ol li, .markdown ul li {
  margin-top: 5px;
  font-size: 1em;
  line-height: 1.6;
}

.markdown ol li input[type="checkbox"], .markdown ul li input[type="checkbox"] {
  width: 1.1em;
  height: 1.1em;
  vertical-align: middle;
  margin-right: .2em;
}

.markdown p {
  font-size: var(--post-p-font-size);
  margin-top: 0;
  margin-bottom: 25px;
  line-height: 1.7;
}

.markdown table {
  border-spacing: 0;
  border-collapse: collapse;
  margin-top: 0;
  margin-bottom: 25px;
  display: block;
  overflow: auto;
}

.markdown table th {
  border: 1px solid var(--post-table-border-color);
  padding: 10px 20px;
  font-size: 1.125em;
  font-weight: 600;
}

.markdown table td {
  border: 1px solid var(--post-table-border-color);
  padding: 10px 20px;
  font-size: 1em;
}

.markdown table tr:nth-of-type(2n) {
  background: var(--post-table-bg);
}

.markdown .highlight {
  border-radius: 6px;
  margin: 0 0 25px;
  overflow: auto;
}

.markdown .highlight table {
  max-width: 100%;
  width: 100%;
  margin-bottom: 0;
  display: table;
}

.markdown .highlight .code {
  width: 100%;
  padding: 0;
}

.markdown .highlight td {
  border: none;
}

.markdown .highlight pre {
  color: var(--post-highlight-text-color);
  line-height: 1.5;
  font-family: var(--post-codeblock-font-family);
  border-radius: 0;
  margin-bottom: 0;
}

